import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { DATE, InputGroupInline, InputType, isEmpty, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { CONSOLIDAR_ENUM_VALUES, NORMAL } from '../../../../../models/enumerated/ger/consolidarEnum';
import { PLANO_CONTA_ORDEM_ENUM_VALUES } from '../../../../../models/enumerated/mcg/planoContaOrdemEnum';
import { CONTA } from '../../../../../models/enumerated/mcg/razaoOrdenacaoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import PlanoContaIS from '../../../../components/mcg/planoContaIS/PlanoContaIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, PlanoContaRV, REPORT_EVENT, REPORT_TITLE } from './planoContaRV';

const DEFAULT_VALUES: PlanoContaRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  consolidar: NORMAL.value,
  contaCaixa: true,
  exibirFormatacaoValor: true,
  gerarCabecalhoPadraoAdministradora: true,
  idsContas: [],
  impDtAtualizacao: true,
  ordem: CONTA.value,
  rodape: true,
  saldos: true,
  tipoConta: true,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATE.value),
  exibirConfiguracaoDadosEmpresa: false,
  exibirSegmentacaoFolha: true,
};

const PlanoContaReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<PlanoContaRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [isMatriz, setIsMatriz] = useState(false);

  useEffect(
    () => {
      if (!isEmpty(globalReducer.globalParameter.empresa)) {
        const {
          empresa: { matriz, centralizar, grupoEmpresa },
        } = globalReducer.globalParameter;
        setTimeout(() => {
          formProps.setValue('idGerGrupoEmpresaAtual', grupoEmpresa.id, true);
          if (matriz === null) {
            formProps.setValue('isAcumularMatrizFilial', centralizar);
            setIsMatriz(true);
          }
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      title="Plano de Contas"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMCG(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <InputGroupInline formProps={formProps} id="idsGerGrupoEmpresaPais[]" hidden={true} />
        <InputGroupInline formProps={formProps} id="idGerGrupoEmpresaAtual" hidden={true} />
        <Row>
          <Col>
            <PlanoContaIS
              formProps={formProps}
              id="idsContas"
              label="Contas"
              placeholder="Caso nenhuma conta seja informada todas serão utilizadas"
              inputSearchProps={{ selectProps: { isMulti: true } }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="ordem"
              inputDropDownProps={{
                optionsProvider: PLANO_CONTA_ORDEM_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              isPrependLabel={true}
              label="Ordem de Impressão"
              placeholder="Informe a Ordem de Impressão"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="consolidar"
              inputDropDownProps={{
                optionsProvider: CONSOLIDAR_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              isPrependLabel={true}
              label="Consolidação"
              placeholder="Selecione o Tipo de Consolidação"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        {isMatriz ? (
          <FormGroup tag={Row}>
            <Col>
              <CustomInput
                type="switch"
                label="Acumular Matriz e Filiais"
                name="isAcumularMatrizFilial"
                id="isAcumularMatrizFilial"
                innerRef={formProps.register}
              />
            </Col>
          </FormGroup>
        ) : null}
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="contaCaixa" label="Destacar Conta Caixa" name="contaCaixa" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="rodape" label="Imprimir Totalizador" name="rodape" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="saldos" label="Imprimir Saldos" name="saldos" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="tipoConta" label="Imprimir Tipo da Conta" name="tipoConta" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              id="impDtAtualizacao"
              label="Imprimir Data de Atualização de Saldo"
              name="impDtAtualizacao"
              innerRef={formProps.register}
              type="switch"
            />
          </Col>
        </FormGroup>
      </ReportForm>
    </WindowComponent>
  );
};

export default PlanoContaReportView;
