import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/planoConta';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

export interface PlanoContaInputSearchProps extends InputProps {
  analitica?: boolean;
  sintetica?: boolean;
  idGrupoPlanoConta?: number;
  filtroContaResultado?: SearchExpression[];
}

const PlanoContaIS: FC<PlanoContaInputSearchProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const getSearchExpression = (): SearchExpression[] => {
    const expressions: SearchExpression[] = [];
    if (props.analitica) {
      expressions.push({
        alias: 'c',
        conditionalGroup: 'c_',
        fieldName: 'analitica',
        value: true,
      });
    }
    if (props.sintetica) {
      expressions.push({
        alias: 'c',
        conditionalGroup: 'c_',
        fieldName: 'analitica',
        value: false,
      });
    }

    if (props.filtroContaResultado) {
      props.filtroContaResultado.forEach(filtro => {
        expressions.push(filtro);
      });
    }

    return [
      {
        conditionalGroup: 'g_',
        expressions,
        fieldName: 'grupoPlanoConta.id',
        value: props.idGrupoPlanoConta || globalReducer.globalParameter.grupoPlanoConta.id,
      },
    ];
  };

  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      searchViewProps={{
        ...props.searchViewProps,
        filtroContaResultado: props.filtroContaResultado,
        hideExtraFields: true,
        useDefaultSelectRow: true,
      }}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'conta',
            text: 'Conta',
          },
          {
            dataField: 'analitica',
            text: 'Analítica',
          },
          {
            dataField: 'codigoReduzido',
            text: 'C.R.',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchExpressions: getSearchExpression,
        searchViewId: 'PlanoContaSearchView',
      }}
      label={props.label ? props.label : 'Plano de Contas'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Plano de Contas'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default PlanoContaIS;
